.user_data {
  position: relative;
  top: -17px;
  height: 50px;
}
.user_data:hover {
  cursor: pointer;
  display: block;
  top: -23px;
}
.user_data:hover > .avatar {
  width: 50px;
  height: 50px;
  -webkit-transition: width .5s, height .5s; /* Safari 3.1 to 6.0 */
  transition: width .5s, height .5s,margin-top .5s; /* Safari 3.1 to 6.0 */
  cursor: pointer; 
 }
.user_data:hover > .user_post {
  margin-left: 54px;
  transition: margin-left .4s ease;
  font-size: 20px;
  margin-top: -13px;
    transition:margin-left .4s ease,margin-top .4s ease;
  height: 51px;
}
#users {
margin: 0;
padding: 0;
width: 100%;
}

 
.secondary_data {
  opacity: 0;
  transition: opacity 1s ease;
  margin-left: 8px;
  display: block;
  position: relative;
  top: -22px;
}
.user_data:hover > .secondary_data {
    transition: opacity .4s ease;
  opacity: 1;
}
.secondary_data ul li {
  display: inline;
  margin: 5px;
  background-color: #e4e4e4;
  padding: 5px;
  border-radius: 5px;
  font-weight: 600;
}
.single_message {
  animation-duration: .6s;
  animation-name: popin;
}

@keyframes popin {
  10% {
    background-color: white;
         opacity: 0;
         margin-top: 0px;
  }

  50% { 
    background-color: rgb(5, 15, 62) !important;
       opacity: .2;
       margin-top: 20px;

  }
  100% {
    background-color: rgb(5, 15, 62) !important;
        opacity: 1;
        margin-top: 0px;

  }
}
#chat {
  height: 70vh;
  overflow-y: scroll;
}
